          <div></div>
          <div></div>
          <div></div>
        </div>
      </div>
    </div>
  </header>
</template>
<script>
import LazyImage from "@/components/LazyImage.vue";
import Avatar from "@/components/Avatar";
export default {
  name: "Header",
  components: { LazyImage, Avatar },
  data: () => ({
    scrolled: false,
    categories: [],
    results: [],
    filterKey: null,
    langSwitcherActive: false,
  }),
  computed: {
    user() {
      return this.$store.getters.user || {};
    },
    needSubscription() {
      return this.empty(this.user.subscription);
    },
  },
  beforeCreate() {
    this.$http.get("/ghost/tvplus/categories")
      .then((response) => {
        this.categories = response.data.categories;
      });
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
  methods: {
    handlePaginationSearch() {
      if (this.filterKey) {
        let url = `ghost/tvplus/search?q=${this.filterKey}`;
        this.$http.get(url).then((response) => {
          this.results = response.data.rows.data;
          this.currentPage = response.data.rows.current_page;
          this.totalRows = response.data.rows.total;
        });
      }
    },
    handleScroll() {
      this.scrolled = window.scrollY > 100;
    },
  },
  watch: {
    $route() {
      this.results = [];
      this.filterKey = null;
      this.$nextTick(() => {
        var activeLink = document.querySelector(
          "a.barca-tv-nav__item-text[href='" + location.pathname + "']"
        );
        if (activeLink) {
          activeLink.classList.add("barca-tv-nav__item-text--active");
        }
      });
      document
        .querySelector(".barca-tv-nav")
        .classList.remove("barca-tv-nav--search");
      document
        .querySelector(".barca-tv-search")
        .classList.remove("barca-tv-search--open");
      document.documentElement.classList.remove("u-overflow-hidden");
      document
        .querySelector(".barca-tv-nav")
        .classList.remove("barca-tv-nav--open");
    },
  },
};
</script>
<style>
@media (max-width: 52.5em) {
  .user-options {
    min-width: unset;
  }
}
</style>
