<template>
  <div class="hp-modal">
    <div
      class="modal fade show"
      tabindex="-1"
      id="subscription-change"
      aria-modal="true"
      role="dialog"
      style="display: block; padding-left: 0px"
    >
      <div :class="`modal-dialog ${modalSize}`">
        <div class="modal-content">
          <a
            href="javascript:void(0)"
            class="close"
            aria-label="Close"
            @click.prevent="handleClose"
            ><em class="icon ni ni-cross"></em
          ></a>
          <div :class="`modal-body ${bodySize}`">
            <slot name="body"></slot>
          </div>
          <div class="modal-footer bg-lighter" v-if="hasFooter">
            <slot name="footer"></slot>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-backdrop fade show"></div>
  </div>
</template>
<script>
export default {
  name: "Modal",
  props: {
    modalSize: {
      type: String,
      default: "modal-lg",
    },
    bodySize: {
      type: String,
      default: "modal-body-md",
    },
    hasFooter: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    handleClose() {
      this.$store.commit("CREATE_AUTH_SESSION", {});
      this.$emit("close");
    },
  },
};
</script>
