<template>
  <Login v-if="authSession.action == 'LOGIN'" />
  <Plans v-else-if="authSession.action == 'PLANS_SELECTION'" />
</template>
<script>
import Login from "./Login.vue";
import Plans from "./Plans.vue";
import { mapState } from "vuex";
export default {
  name: "Provider",
  components: {
    Login,
    Plans,
  },
  computed: {
    ...mapState({
      authSession: (state) => state.authSession || {},
    }),
  },
  watch: {
    authSession() {
      if (this.authSession.action)
        document.documentElement.classList.add("auth-open");
      else document.documentElement.classList.remove("auth-open");
    },
  },
};
</script>
