<template>
  <modal modal-size="default" body-size="default">
    <div slot="body">
      <div class="nk-block nk-block-middle nk-auth-body">
        <div class="brand-logo pb-5">
          <router-link :to="{ name: '' }" class="logo-link"
            ><img
              class="logo-dark logo-img logo-img-lg"
              :src="appLogo"
              alt="logo"
              style="width: 150px"
          /></router-link>
        </div>
        <div class="nk-block-head">
          <div class="nk-block-head-content">
            <h5 class="nk-block-title">Login</h5>
            <div class="nk-block-des"><p>Log into Account</p></div>
          </div>
        </div>
        <form action="" method="POST" @submit.prevent="authenticateUser">
          <div class="form-group add_top_10">
            <div class="fl-wrap fl-wrap-input fl-is-required">
              <label for="name" class="fl-label">Email or Username</label
              ><input
                type="text"
                name="Username"
                v-validate="'required'"
                :class="{ 'is-invalid': errors.has('Username') }"
                v-model="credentials.email"
                id="name"
                class="form-control required fl-input error"
                placeholder="Enter your email address"
              /><span for="name" class="error" v-if="errors.has('Username')"
                >Required</span
              >
            </div>
          </div>

          <div class="form-group mt-3">
            <div class="fl-wrap fl-wrap-input fl-is-required">
              <label for="name" class="fl-label">Password</label
              ><input
                type="Password"
                name="password"
                :class="{ 'is-invalid': errors.has('password') }"
                v-validate="'required'"
                v-model="credentials.password"
                id="password"
                class="form-control required fl-input error"
                placeholder="Password"
              /><span for="name" class="error" v-if="errors.has('password')"
                >Required</span
              >
            </div>
          </div>

          <div class="alert alert-fill alert-danger my-2" v-if="invalidLogin">
            <span>Invalid Password or Email addess</span>
          </div>
          <div
            class="alert alert-fill alert-success fade show my-2"
            role="alert"
            v-if="loginSucceed"
          >
            <strong>Success!</strong> Login Success. Redirecting...
          </div>
          <div class="mb-0 d-flex flex-nowrap align-items-center">
            <wolf-button
              caption="Sign In"
              button-type="submit"
              @btn-role="authenticateUser"
              :disabler="!credentials.email || !credentials.password"
              activator="signing"
              classes="forward"
              >Sign in</wolf-button
            >
            <h6 class="text-center mb-0 ml-2">Or Log with</h6>
          </div>
        </form>
        <div class="py-4">
          <div class="d-flex w-100 align-items-center">
            <div class="w-50 pr-1">
              <button
                class="btn bg-white py-1 w-100"
                style="border: 1px solid #e6e6e6"
                type="button"
                @click="authenticate('facebook')"
              >
                <img
                  :src="`${publicPath}img/facebook.png`"
                  style="width: 32px"
                  alt="facebook"
                />
                Facebook
              </button>
            </div>
            <div class="w-50 pl-1">
              <button
                class="btn bg-white py-1 w-100"
                style="border: 1px solid #e6e6e6"
                type="button"
                @click="authenticate('google')"
              >
                <img
                  :src="`${publicPath}img/google.png`"
                  style="width: 32px"
                  alt="facebook"
                />
                Google
              </button>
            </div>
          </div>
        </div>
        <div class="form-note-s2 pt-4">
          Don't have an account ?
          <a href="javascript:void(0)"><strong>Register instead</strong></a>
        </div>
      </div>
    </div>
  </modal>
</template>
<script>
import Modal from "@/components/Modal.vue";
export default {
  components: { Modal },
  data: () => ({
    credentials: {
      email: null,
      password: null,
    },
    invalidLogin: false,
    loginSucceed: false,
  }),
  methods: {
    authenticate(network) {
      const _this = this;
      const hello = this.hello;
      hello(network)
        .login()
        .then(() => {
          const authRes = hello(network).getAuthResponse();
          /*
          performs operations using the token from authRes
        */
          let output = JSON.stringify(authRes, undefined, 4);
          _this.authRes = output;
          console.log(output);
          hello(network)
            .api("me")
            .then(function (profile) {
              /*
            performs operations using the user info from profile
          */
              let output = JSON.stringify(profile, undefined, 4);
              _this.profileRes = output;
              console.log(output);
            });
        });
    },
    authenticateUser() {
      this.$http
        .post("auth/login", this.formData(this.credentials))
        .then((response) => {
          if (response.data.status) {
            const token = response.data.access_token;
            if (token) {
              localStorage.setItem("token", token);
            }
            this.invalidLogin = false;
            this.loginSucceed = true;
            if (location.href == this.authSession.redirect_url)
              this.$store.commit("CREATE_AUTH_SESSION", {});
            else location.replace(this.authSession);
          }
        })
        .catch((err) => {
          console.log(err.response.status);
          if (err.response.status == 401) {
            this.invalidLogin = true;
            this.loginSucceed = false;
          }
        });
    },
  },
};
</script>
