<template>
  <modal>
    <div slot="body">
      <div class="sp-package text-center">
        <div class="sp-package-head">
          <h4 class="title">Select Subscription</h4>
          <p class="text-soft">
            This subscription will become effective on
            {{ chatDate(new Date()) }} on your account.
          </p>
        </div>
        <ul class="sp-package-list">
          <li
            class="sp-package-item"
            v-for="(card, i) in cards"
            :key="'card' + i"
          >
            <input
              class="sp-package-choose"
              type="radio"
              name="subscription-pack-plan"
              :id="'pack-plan_' + card.id"
              :value="card.id"
              v-model="selectedPack"
            /><label class="sp-package-desc" :for="'pack-plan_' + card.id"
              ><span class="sp-package-info"
                ><span class="sp-package-title title"
                  >{{ card.title }}
                  <span class="badge bg-primary rounded-pill" v-if="i == 0"
                    >Popular</span
                  ></span
                ><span class="sp-package-detail"
                  >Unlimited TV + Access
                </span></span
              ><span class="sp-package-price"
                ><span class="sp-package-amount yearly"
                  ><span class="amount"
                    >RWF
                    {{
                      addComma(card.promotionalprice) || addComma(card.price)
                    }}</span
                  ><span class="text-soft"
                    >{{ card.expiration_period }}
                    {{ card.expiration_unit }}</span
                  ></span
                ></span
              ></label
            >
          </li>
        </ul>
        <div class="sp-package-action">
          <button
            class="btn btn-primary"
            type="button"
            @click="handleContinue"
            :disabled="!selectedPack"
          >
            Confirm and continue</button
          ><a
            class="btn btn-dim btn-danger"
            href="javascript:void(0)"
            @click.prevent="handleCancel"
            >Cancel</a
          >
        </div>
      </div>
    </div>
  </modal>
</template>
<script>
import Modal from "@/components/Modal.vue";
export default {
  components: { Modal },
  data: () => ({
    selectedPack: null,
    cards: [],
  }),
  beforeCreate() {
    this.$http.get("ghost/membership-cards")
      .then((response) => {
        this.cards = response.data.cards;
      });
  },
  methods: {
    handleContinue() {
      let card = this.cards.find((element) => element.id == this.selectedPack);
      console.log(card);
      window.location.replace(
        this.$router.resolve({
          name: "servicesCheckout",
          query: {
            service: "membership",
            s_ref: card.code,
            slug: card.slug,
          },
        }).href
      );
    },
    handleCancel() {
      this.$store.commit("CREATE_AUTH_SESSION", {});
    },
  },
};
</script>
