<template>
  <div class="">
    <app-header />
    <div class="body-content js-body-content">
      <main id="main">
        <router-view />
      </main>
    </div>
    <auth-provider></auth-provider>
    <app-footer />
  </div>
</template>
<script>
import Footer from "./Views/Shared/Footer";
import Header from "./Views/Shared/Header";
import AuthProvider from "@/components/Auth/Provider.vue";
export default {
  name: "app",
  components: {
    "app-header": Header,
    "app-footer": Footer,
    AuthProvider,
  },
  data: () => ({
    isMoboEnv: false,
  }),
  computed: {
    currentUser() {
      return this.$store.getters.user || {};
    },
  },
  beforeMount() {
    if (this.isUserLoggedIn) {
      this.$http.get("auth/me").then((response) => {
        this.$store.dispatch("setCurrentUser", { ...response.data.user });
      });
    }
  },
  created() {
    this.checkMobileEnv();
    this.getSettings();
  },
  methods: {
    checkMobileEnv() {
      if (matchMedia) {
        const mq = window.matchMedia("(max-width: 768px)");
        this.isMoboEnv = mq.matches;
        try {
          // Chrome & Firefox
          mq.addEventListener("change", () => {
            this.isMoboEnv = mq.matches;
          });
        } catch (e1) {
          try {
            // Safari
            mq.addListener(() => {
              this.isMoboEnv = mq.matches;
            });
          } catch (e2) {
            console.error(e2);
          }
        }
        //this.WidthChange(mq);
      }
    },
    WidthChange(mq) {
      this.isMoboEnv = mq.matches;
    },
  },
};
</script>
